<template>
    <layout>
        <div class="shopping-cart">
            <cube-scroll
                ref="scroll"
                :data="shopCartList">
                <cube-swipe>
                <transition-group class="list-wrapper" name="swipe" tag="ul">
                    <li v-for="(item, index) in shopCartList" class="list-item-wrap" :key="index">
                        <cube-swipe-item
                        ref="swipeItem"
                        :btns="item.btns"
                        :index="index"
                        class="list-item"
                        @btn-click="onBtnClick">
                            <cube-checkbox @input="_updateTotal" v-model="item.item.checked" :option="{label: ''}"></cube-checkbox>
                            <div class="img">
                                <img :src="`/picture/${item.item.itemImg}`" alt="">
                            </div>
                            <div class="info">
                                <div class="top">
                                    <p class="name">{{item.item.itemName}}</p>
                                    <p class="tip">{{item.item.goodsTexture}}</p>
                                </div>
                                <div class="price">￥{{item.item.itemPrice}} <span class="cm">/件</span></div>
                            </div>
                            <div class="buy-num">
                                <i class="add" @click="_add(item.item)"></i>
                                <span class="num">{{item.item.num}}</span>
                                <i class="less" @click="_less(item.item)"></i>
                            </div>
                        </cube-swipe-item>
                    </li>
                </transition-group>
                </cube-swipe>
                <div class="empty" v-show="shopCartList.length===0">
                    <p>购物车竟然是空的~</p>
                </div>
            </cube-scroll>
        </div>
        <div class="shop-car-num">
            <div class="piece">
                共{{numTotal}}件
            </div>
            <div class="total">
                合计 ￥{{priceTotal}}
            </div>
            <div class="btn" @click="_order">下单</div>
        </div>
    </layout>
</template>
<script>
import { queryShopCart, queryPrice, addShopCart, deleteShopCart } from "@/api/cart.js";
export default {
    name: 'shopping-cart',
    data() {
        return {
            title: 'Current City: BEIJING',
            shopCartList: [],
            numTotal: 0,
            priceTotal: 0,
            checkedList: [],
        };
    },
    created() {
       this._queryShopCart(); 
    },
    methods: {
        _order() {
            if (this.checkedList.length === 0) {
                const toast = this.$createToast({
                    txt: '您还没有选择商品~',
                    type: 'txt',
                    time: 2000,
                })
                toast.show()
            } else {
                this.$router.push({ path: '/order', query: { shopId: this.checkedList.join(',') } });
            }
        },
        onBtnClick(btn, index) {
            if (btn.action === 'delete') {
                this.$createActionSheet({
                    title: '确认要删除吗',
                    active: 0,
                    data: [
                        {content: '删除'}
                    ],
                onSelect: () => {
                    this._deleteShopCart(this.shopCartList[index].item.id);
                    this.shopCartList.splice(index, 1);
                    this.$refs.swipeItem[index].shrink();
                    this._updateTotal();
                }
                }).show()
            } else {
                this.$refs.swipeItem[index].shrink()
            }
        },
        // 列表
        _queryShopCart() {
            let params = {
                userId: 1,
            }
            this.shopCartList = [];
            queryShopCart(params).then(res => {
                res.ShopCartList.forEach(item => {
                    item.checked = false;
                    let obj = {
                        item: item,
                        btns: [
                            {
                                action: 'delete',
                                text: '删除',
                                color: '#ff3a32',
                            }
                        ]
                    }
                    this.shopCartList.push(obj)
                })
            }).catch(err => {
            })
        },
        // 获取总计总价
        _queryPrice() {
            let params = {
                shopId: this.checkedList.join(','),
            }
            queryPrice(params).then(res => {
                this.numTotal = res.totalNum;
                this.priceTotal = res.totalPrice;
            }).catch(err => {
            })
        },
        // 更新总计总价
        _updateTotal() {
            this.checkedList = [];
            this.shopCartList.forEach(item => {
                if (item.item.checked) {
                    this.checkedList.push(item.item.id)
                }
            })
            if (this.checkedList.length === 0) {
                this.numTotal = 0;
                this.priceTotal = 0;
            } else {
                this._queryPrice();
            }
        },
        // 加入购物车
        _add(item) {
            if (item.num === 99) {
                const toast = this.$createToast({
                    txt: '宝贝不能再减少了哦~',
                    type: 'txt',
                    time: 2000,
                })
                return;
            }
            let params = {
                userId: 1,
                itemId: item.itemId,
                id: item.id,
                flag: 1,
            }
            const toast = this.$createToast({
                time: 5000,
            })
            toast.show();
            addShopCart(params).then(res => {
                item.num += 1;
                this._updateTotal();
                toast.hide();
            })
        },
        // 减掉1件
        _less(item) {
            if (item.num === 1) {
                const toast = this.$createToast({
                    txt: '宝贝不能再减少了哦~',
                    type: 'txt',
                    time: 2000,
                })
                toast.show()
                return;
            }
            let params = {
                userId: 1,
                itemId: item.itemId,
                id: item.id,
                flag: 2,
            }
            const toast = this.$createToast({
                time: 5000,
            })
            toast.show();
            addShopCart(params).then(res => {
                item.num -= 1;
                this._updateTotal();
                toast.hide();
            })
        },
        // 删除商品
        _deleteShopCart(shopId) {
            let params = {
                shopId,
            }
            const toast = this.$createToast({
                time: 5000,
            })
            toast.show();
            deleteShopCart(params).then(res => {
                toast.hide();
            })
        },
    }
}
</script>
<style lang="stylus" scoped>
.shopping-cart {
    // padding-bottom: 98px;
    height: calc(100vh - 196px);
    .list-wrapper {
        background-color: #FFFFFF;
        .list-item-wrap {
        }
        .list-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 20px 30px;
            border-bottom: 0.5px solid #F2F2F2;
            .img {
                width:180px;
                height:120px;
                border-radius:8px;
                img {
                    width:180px;
                    height:120px;
                    border-radius:8px;
                }
            }
            .info {
                flex: auto;
                height:120px;
                padding-left: 21px;
                .top {
                    margin-top: 10px;
                    position: relative;
                    .name {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 28px;
                        font-family: HiraginoSansGB-W3;
                        color: #333333;
                    }
                    .tip {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 18px;
                        line-height: 24px;
                        font-family: HiraginoSansGB-W3;
                        color: #ffffff;
                        padding: 0 6px;
                        background: #F89771;
                        border-radius:8px;
                    }
                }
                .price {
                    margin-top: 45px;
                    font-size: 24px;
                    font-family: HiraginoSansGB-W3;
                    font-weight: bold;
                    color: #F43030;
                    .cm {
                        font-size: 18px;
                        color: #666666;
                    }    
                }
            }
            .buy-num {
                width: 120px;
                font-size: 0px;
                .add {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    background-image: url(../../../static/image/product/add@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;
                }
                .less {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    background-image: url(../../../static/image/product/js@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;                
                }
                .num {
                    vertical-align: top;
                    display: inline-block;
                    font-size: 24px;
                    font-family: HiraginoSansGB-W3;
                    font-weight: bold;
                    color: #333333;
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                }
            }
        }
    }
    .empty {
        line-height: 36px;
        padding: 24px 0;
        text-align: center;
    }
}

    .shop-car-num {
        position: fixed;
        // bottom: 122px;
        bottom 98px;
        // left: 75px;
        left: 0;
        z-index: 99;
        // width: 600px;
        width: 100%;
        height: 98px;
        line-height: 98px;
        display: flex;
        align-items: center;
        // border-radius: 10px;
        background-color: #ffffff;
        border-top: 0.5px solid #F2F2F2;
        .piece {
            padding-left: 40px;
            font-size: 24px;
            font-weight: bold;
            color: #333333;
        }
        .total {
            flex: 1;
            font-size: 24px;
            padding-left: 24px;
            font-family:HiraginoSansGB-W3;
            font-weight: bold;
            color: #333333;
        }
        .btn {
            width: 160px;
            height: 98px;
            background-color: #4A4C59;
            text-align: center;
            color: #ffffff;
            // border-top-right-radius: 10px;
            // border-bottom-right-radius: 10px;
        }
    }
.cube-checkbox {
    padding: 0;
}
</style>



